@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap');


#navbar {
  width: 100%;
  height: 60px;
  background-color: rgb(3, 0, 0);
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 5px 10px 18px red;
}




.background{
  text-align: center;
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
  background-position: center;
  z-index: -2;
}

button{
  font-family: "bree-serif";
  display: inline-block;
    border: none;
    padding: 0.05rem 2rem;
    margin: 0;
    text-decoration: none;
    color: #ffffff;
    background: #000000;
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
    transform: skewX(-15deg);
}

button:hover{
background: #f00707;
}



.searchOutput{
  background-color: white;
  position: absolute;
  top: 45px;
  right: 4px;
}
.srcAnim{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
}
#library{
  box-sizing: border-box;
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-image: linear-gradient(to right, rgba(3, 0, 0, 0.377), rgba(0, 0, 0, 0.808));
}


.anime{
  background-color: rgba(27, 5, 5, 0.171);
  width: 200px;
  min-width: 100px;
  margin: 20px;
  padding-top: 4px;
}

.anime:hover{
  box-shadow: 5px 5px 5px rgb(250, 250, 250);
}



#libImg{
  width:200px;
  height: 300px;
}

.category{
  font-size: 10px;
}

.categories{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
}

a{
  color: white;
}

.title{
  margin-top: -5px;
  width: 10em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}